import { useState } from 'react';
import {Container, Navbar, Nav} from 'react-bootstrap';
import Carousel from 'react-bootstrap/Carousel';

function Section1(){
    const [activeKey, setActiveKey] = useState('main');
    const handleActive = (selectedKey) => {
        setActiveKey(selectedKey);
    };
    
    return(
        <>
        <Navbar bg="primary" variant="dark" expand="lg" fixed='top'>
            <Container fluid>
                <Navbar.Brand href="#">PROFILE SITE</Navbar.Brand>
                <Navbar.Toggle aria-controls="toggle" />
                <Navbar.Collapse id="toggle">
                    <Nav activeKey={activeKey} onSelect={handleActive}>
                        <Nav.Item>
                            <Nav.Link eventKey="main" href="#main">main</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="work" href="#work">work</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="about" href="#about">about me</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="content" href="#content">content</Nav.Link>
                        </Nav.Item>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
        <Carousel fade id='main'>
            <Carousel.Item>
                <img src={`${process.env.PUBLIC_URL}/images/banner1.jpg`} alt='1' className='mainBg' />
                <Carousel.Caption>
                <h3>몰입하면<br/>끝까지 집중!</h3>
                <p>표정은 무심해도<br/>머릿속은 아이디어 짜는 중</p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
                <img src={`${process.env.PUBLIC_URL}/images/banner2.jpg`} alt='2' className='mainBg' />
                <Carousel.Caption>
                    <h3>느리지만<br/>어떻게든<br/>완성!</h3>
                    <p>절대 농땡이 치는거 아니에요<br/>정말이에요</p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
                <img src={`${process.env.PUBLIC_URL}/images/banner3.jpg`} alt='3' className='mainBg' />
                <Carousel.Caption>
                    <h3>느긋하게<br/>하지만<br/>진심으로!</h3>
                    <p>에너지 충전 중이라서<br/>더 집중할 수 있어요</p>
                </Carousel.Caption>
            </Carousel.Item>
        </Carousel>
        </>
    )
}

export default Section1;